* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Hind Siliguri, sans-serif;
  list-style: none;
}

header {
  background-image: url("fundo.bce0ab17.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 50vh;
  position: relative;
}

header:before {
  content: "";
  opacity: .6;
  background-color: #000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

header .container {
  text-align: center;
  color: #fff;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 54px 0;
  display: flex;
  position: relative;
}

header h1 {
  font-family: Oleo Script, cursive;
}

header p {
  margin: 35px 0;
  font-size: 30px;
}

header a {
  color: #292929;
  background-color: #efed58;
  border-radius: 20px;
  width: 25%;
  padding: 14px;
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
  display: block;
}

.infos {
  background-image: linear-gradient(to right, #eef4b4, #edf39f, #edf189, #eeef72, #efed58, #eeef72, #edf189, #edf39f, #eef4b4);
  justify-content: space-between;
  padding: 80px 120px;
  display: flex;
}

.infos .local, .infos .atracoes {
  text-align: center;
  font-size: 18px;
}

.infos .local img, .infos .atracoes img {
  border-radius: 20px;
  height: 250px;
}

.infos .local h2, .infos .atracoes h2 {
  padding: 15px 0;
}

.alerta {
  text-align: center;
  background-color: #faf8e6;
  border-top: 2px dashed red;
  padding: 24px 0 32px;
}

.alerta .title {
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.alerta .title img {
  height: 50px;
}

.alerta .title h2 {
  font-size: 32px;
}

.alerta ul {
  justify-content: center;
  gap: 40px;
  display: flex;
}

.alerta ul li {
  border-bottom: 2px solid #000;
  font-size: 20px;
}

